// Colors
$color-primary:     #1080B1;
$color-secondary:   #00FFFF;

$light-gray:        #FFF1FF;
$gray:              #DDDDDD;
$dark-gray:         #B6B6B6;
$white:             #FFFFFF;
$black:             #000000;
$blue:              #000066;



// Objects
$text-color:          #212121;
$link-color:          $color-primary;
$link-hover-color:    darken($color-primary, 10%);

$btn-color-primary:   $color-secondary;



// Typography
$font-primary:        'Montserrat', sans-serif;
// $font-secondary:      '', sans-serif;



// Behaviour
$transition-default:     all .2s ease-out;
$transition-animations:  all .7s ease-out;



// Breakpoints
$xs-max:  767px;
$sm:      768px;
$sm-max:  1023px;
$md:      1024px;
$md-max:  1199px;
$lg:      1366px;
